.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    row-gap: 2%;
    margin: 20px 20px 100px 20px;
    
    .insideContainer{
        width: 47%;
        height: 77vh;
        border-radius: 10px;
        box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);

        
    .progressBar{
        margin: 0 auto;
        width: 90%;
        margin-top: 8%;
    }
    }
    
}